html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 1.6rem;
  background-color: #FFFFFF;
  transition: background-color 0.3s ease-in-out;
}

p {
  font-size: 2rem;
  color: #575757;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 1.5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  padding-bottom: 1rem;
  border-bottom: 2px dashed #e1e1e1;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

span {
  font-weight: bold;
  color: #575757;
}

a {
  text-decoration: none;
  color: #575757;
}

.dark-mode {
  background-color: #333333;
  transition: background-color 0.3s ease-in-out;
  border-bottom: 1px solid #000000;
}
.dark-mode li {
  color: #FFFFFF;
}
.dark-mode .header h1, .dark-mode .header h2 {
  color: #FFFFFF;
}
.dark-mode p, .dark-mode span {
  color: #FFFFFF;
  transition: color 0.3s ease-in-out;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
  transition: color 0.3s ease-in-out;
}
.dark-mode .parte-derecha {
  background-color: #615d5d;
  transition: background-color 0.3s ease-in-out;
}
.dark-mode .parte-derecha i.fa-gear, .dark-mode .parte-derecha i.fa-users {
  filter: invert(100%);
}

.header {
  background-color: #294d8f;
  transition: background-color 0.3s ease-in-out;
  text-align: center;
  padding-bottom: 2rem;
}
.header picture img {
  border-radius: 25%;
}
.header div.abajo {
  margin-top: 2rem;
}
@media (max-width: 768px) {
  .header h1 {
    font-size: 2.8rem;
  }
  .header h2 {
    font-size: 2.6rem;
  }
}
@media (min-width: 768px) {
  .header {
    text-align: start;
  }
}
.header h1 {
  color: #FFFFFF;
  font-weight: bold;
}
.header h2 {
  font-weight: bold;
  color: #d1cdcd;
  transition: color 0.3s ease-in-out;
}
.header picture {
  width: 10rem;
  height: 10rem;
  display: inline-block;
}

.dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  display: inline-block;
  filter: invert(100%);
}
.dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .dark-mode-boton {
    margin-top: 0;
  }
}

.contenido-header div.imagenes-header {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contenido-header {
    display: flex;
    padding: 1rem 0;
    justify-content: space-between;
  }
  .contenido-header div.imagenes-header {
    flex-direction: row;
    gap: 2rem;
    align-items: center;
  }
}

@media (max-width: 768px) {
  .contenido-main {
    text-align: justify;
  }
  .contenido-main p {
    font-size: 1.6rem;
  }
  .contenido-main h3 {
    font-size: 2.4rem;
  }
  .contenido-main h4 {
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .contenido-main {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 4rem;
    margin-right: 0;
  }
}

.parte-izquierda p {
  margin: 1rem 0;
}
.parte-izquierda h4 {
  font-size: 2rem;
  margin-top: 1.5rem;
}

@media (min-width: 768px) {
  .contenido-exp, .contenido-cert {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 1rem;
    align-items: left;
  }
}

.parte-derecha {
  background-color: #d1cdcd;
  transition: background-color 0.3s ease-in-out;
  padding: 2rem;
}
@media (max-width: 768px) {
  .parte-derecha {
    border-radius: 1rem;
    margin-bottom: 1rem;
  }
}
.parte-derecha li {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 1rem 0;
  color: #575757;
}
.parte-derecha li p {
  font-size: 1.5rem;
}
.parte-derecha li i {
  padding-right: 1rem;
}

i {
  color: #000000;
}
i.fa-brain {
  color: #b34c66;
  transition: color 0.3s ease-in-out;
}
i.fa-bullhorn {
  color: #294d8f;
  transition: color 0.3s ease-in-out;
}

.contenido-app {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  align-items: center;
}
.contenido-app img {
  width: 10rem;
  height: 10rem;
}